*{
    font-family: 'Comic Neue', sans-serif;
}

#container {
    height: 100vh;
    display: flex;
    flex-direction: column;
}

#row_block {
    display: flex;
    flex-direction: row;
    height: 80vh;
}

#sidebar {
    width: 8%;
    border-right: 1px solid #e3e5e8;
    float: left;
}

#content {
    width:92%;
    overflow-y: scroll;
    float: left;
}

::-webkit-scrollbar {
      width: 15px;
  }

::-webkit-scrollbar-track {
    background: #e3e5e8;
}

::-webkit-scrollbar-thumb {
    background: linear-gradient(to bottom, gray, black);
    border-radius: 8px;
    border: 3px solid #e3e5e8;
}

